import Slider from "react-slick";

// import image=
import carouselImg1 from '../assets/img/carousel-img-1.webp'
import carouselImg2 from '../assets/img/carousel-img-2.webp'
import carouselImg3 from '../assets/img/carousel-img-3.webp'
import carouselImg4 from '../assets/img/carousel-img-4.webp'

import '../assets/css/carousel.css'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import ButtonComponent from './Button'

function Carousel(){
    var settings = {
        dots: true,
        infinite: true,
        speed: 800,
        autoplay: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        fade: true,
        cssEase: 'linear'
      };
    return(
        <div className="slider-wrapper mt-114">
                <Slider {...settings} className="slider-main">
                <div class="slider-item">
                    <div className='slider-text'>
                        <h5 className='slider-item-title'>BN B HU LU VIETNAM</h5>
                        <ButtonComponent link="/contact" text="Nhn t vn"/>
                    </div>
                    <img src={carouselImg1} class="slider-img slider-img-1" alt="" />
                </div>
                <div class="slider-item">
                    <div className="slider-text">
                            <h5 className='slider-item-title'>M HNH QUN BN B HU</h5>
                            <ButtonComponent link="/contact" text="Nhn t vn"/>
                    </div>
                    <img src={carouselImg2} class="slider-img slider-img-1" alt="" />
                </div>
                <div class="slider-item">
                    <div className='slider-text'>
                        <h5 className='slider-item-title'>CM G DA GIN LU VIETNAM</h5>
                        <ButtonComponent link="/contact" text="Nhn t vn"/>
                    </div>
                    <img src={carouselImg3} class="slider-img slider-img-3" alt="" />
                </div>
                <div class="slider-item">
                    <div className='slider-text'>
                        <h5 className='slider-item-title'>M HNH QUN CM G DA GIN</h5>
                        <ButtonComponent link="/mo-hinh-quan-com-ga-da-gion" text="Nhn t vn"/>
                    </div>
                    <img src={carouselImg4} class="slider-img" alt="" />
                </div>
                </Slider>
    </div>
    )
}

export default Carousel