import { useNavigate } from "react-router";
import { useEffect } from "react";

import Header from "./Header"
import Footer from "./Footer"
import CallBtn from "./CallBtn"

import '../assets/css/blog.css';

import BlogArticleImg1 from '../assets/img/blog1-img.jpg'
import BlogArticleImg2 from '../assets/img/blog2-img.jpg'

function Blog(){
    useEffect(() => {
                    window.scrollTo(0, 0);
                  }, []);
                  
    const navigate = useNavigate();

  const renderArticle1 = () => {
    // Basic navigation to '/about'
    navigate('/bai-viet-ve-noi-ham-dien');
  };

  const renderArticle2 = () => {
    navigate('/4-dieu-can-luu-y-khi-chon-dia-diem-kinh-doanh-an-uong')
  }

    return (
        <div className="blog">
            <Header active="3"/>
            <section className="blog-wrapper app-item mt-114">
                <h2 className="home-section-title">Bi vit ni bt</h2>
                <section className="blog-gallery container">
                    <div className="row blog-gallery-wrapper">
                    <div className="col-lg-4 col-sm-12">
                            <div className="blog-card" onClick={renderArticle2}>
                                <img src={BlogArticleImg2} alt="nh i din cho bi vit v ni hm in" />
                                <div className="blog-card-content">
                                    <h4 className="blog-card-title">4 iu cn lu  khi chn 1 a im kinh doanh n ung!</h4>
                                    <p>Tc gi: L Thun Li</p>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-4 col-sm-12">
                            <div className="blog-card" onClick={renderArticle1}>
                                <img src={BlogArticleImg1} alt="nh i din cho bi vit v ni hm in" />
                                <div className="blog-card-content">
                                    <h4 className="blog-card-title">Ni hm in - Gii php nu nng hin i, tin li v an ton</h4>
                                    <p>Tc gi: L Thun Li</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </section>
            <CallBtn />
            <Footer />
        </div>
    )
}

export default Blog