import { useEffect } from "react";
import { Link } from "react-router";

// import components
import ButtonComponent from "./Button";
import Header from "./Header";
import Footer from "./Footer";
import Carousel from "./Carousel";
import HomeServiceCard from "./HomeServiceCard";
import ContactBanner from "./ContactBanner";
import CallBtn from "./CallBtn";


// import css files
import '../assets/css/home.css'
import '../assets/css/responsive.css'

// import image
import productImg1 from '../assets/img/product-item-1.jpg'
import productImg2 from '../assets/img/product-item-2.jpg'
import productImg3 from '../assets/img/product-item-3.jpg'
import founderImg from '../assets/img/founder-img.jpg'

function Home(){
    useEffect(() => {
            window.scrollTo(0, 0);
          }, []);
    return(

        <div className="home-wrapper">
            <Header active="1"/>
            <Carousel title="LUVIETNAM" text="T VN - THIT K - THI CNG SETUP QUN - O TO BP"/>
            {/* about section */}
            <section className="home-about-section container-fluid app-item">
                <div className="row align-items-center justify-content-between">
                    <div className="home-about-textbox col-lg-8 col-md-6 col-sm-12">
                        <h2 className="home-section-title">V chng ti</h2>
                        <p><b>Luvietnam - Thit k v setup qun n chuyn nghip.</b> <p>Chng ti bin  tng ca bn thnh khng gian m thc c o, thu ht v hiu qu. 
                            Tin cy Luvietnam  khi u hnh trnh kinh doanh y cm hng!</p> </p>
                        <ButtonComponent link='/about' text="Tm hiu thm" />
                    </div>
                    <img src={founderImg} alt="luvietnam logo" className="col-lg-3 col-md-4 col-sm-12 app-section-img" loading="lazy" />
                </div>
            </section>

            {/* service intro section */}
            <section className="home-service-section container-fluid app-item">
                <h3 className="home-section-title">Chng ti cung cp</h3>
                <div className="home-service-card__wrapper row">
                    <HomeServiceCard title="T vn - Thit k qun" icon="fa-solid fa-lightbulb"/>
                    <HomeServiceCard title="Set up qun" icon="fa-solid fa-gears"/>
                    <HomeServiceCard title="o to bp" icon="fa-solid fa-kitchen-set"/>
                </div>
            </section>

            {/* product section */}
            <section className="home-product-section container-fluid app-item">
                <div className="row">
                    <div className="home-product-textbox">
                        <h2 className="home-section-title">Sn phm</h2>
                        <p>Chng ti mang n gii php ton din vi ni tht cht lng v nguyn liu ti ngon, gip bn xy dng khng gian v thc n hon ho. ng hnh cng Luvietnam  to nn du n ring cho qun n ca bn!</p>
                    </div>
                    <div className="home-product-gallery container">
                        <div className="row home-product-gallery-wrapper">
                            <div className="col-lg-4 col-md-4 col-sm-12 home-product-gallery__item">
                                <img src={productImg1} alt="product 1" className="" loading="lazy" />
                            </div>
                            <div className="col-lg-4 col-md-4 col-sm-12 home-product-gallery__item">
                                <img src={productImg2} alt="product 2" className="" loading="lazy" />
                            </div>
                            <div className="col-lg-4 col-md-4 col-sm-12 home-product-gallery__item">
                                <img src={productImg3} alt="product 3" className="" loading="lazy" />
                            </div>
                        </div>
                        <Link to="/product">Xem thm <i class="fa-solid fa-arrow-right"></i></Link>
                    </div>
                </div>
            </section>
            <ContactBanner />
            <CallBtn />
            <Footer />
        </div>
    )
}

export default Home;